.gallery {
  &__link {
    margin-right: 4px;
  }

  &__button {
    display: block;
    margin: 0 auto;
    width: 100%;
    height: 56px;

    border: 2px solid $primary-color;
    border-radius: 8px;
    color: $primary-color;

    &:hover {
      transition: color $transition-time ease-in-out,
        background-color $transition-time ease-in-out;
      color: $secondary-color;
      background-color: $primary-color;
    }
    @media (min-width: $medium-resolution) {
      width: 188px;
    }
  }

  &__header {
    text-align: center;
    margin-bottom: 61px;
  }
}
